import React, {useEffect} from "react";
import "./BubbleRow.css";
import Bubble from "./Bubble";
import {Row, Col} from "antd";
import {TagOutlined, TagFilled} from "@ant-design/icons/lib";
import {useSessionStorageState} from "@umijs/hooks";

const BubbleRow = ({number, timerStopped$, enableButton$, totalQuestions}) => {
    const [timerActive, setTimerActive] = useSessionStorageState("timerActive");
    const [selectedChoice, setChoice] = useSessionStorageState("answerData", {});
    const [marked, setMarked] = useSessionStorageState("markedQuestions", []);
    const choices = ["A", "B", "C", "D"];
    const tagRotation = -45;

    timerStopped$.useSubscription(val => {
        if (val === "started") setTimerActive(true);
        else setTimerActive(false);
    })

    useEffect(() => {
        if (!(selectedChoice === {})){
            if (Object.keys(selectedChoice).length >= totalQuestions) enableButton$.emit(false);
            else enableButton$.emit(true);
        }
    }, [marked, selectedChoice, number, enableButton$, totalQuestions]);

    const markRow = () => {
        if (timerActive) {
            setMarked(marked => ({...marked, [number]: !marked[number]}));
        }
    }

    const handleOnChange = e => {
        setChoice(selectedChoice => {
            return (
                {...selectedChoice, [number]: e.target.value}
            )
        });
    }

    return (
        <div onChange={e => handleOnChange(e)}>
            <Row>
                <Col span={1} className={`bookmark ${!timerActive ? "disabled-bookmark" : ""}`}>
                    <div onClick={markRow}>
                        {marked[number] ? <TagFilled rotate={tagRotation}/> : <TagOutlined rotate={tagRotation}/>}
                    </div>
                </Col>
                <Col span={1} className={"question-number"}>
                    <div>{number}.</div>
                </Col>
                <Col span={5} style={{verticalAlign: "middle"}}>{choices.map(choice => <Bubble letter={choice}
                                                                                               key={choice}
                                                                                               isSelected={selectedChoice[String(number)] === choice}
                                                                                               number={number}
                                                                                               disabled={!timerActive}
                />)}</Col>
            </Row>
        </div>

    )

}

export default BubbleRow;