import React, {useEffect, useRef} from "react";
import {useSessionStorageState} from '@umijs/hooks';
import Countdown from "react-countdown";
import {Button, Popover, message} from "antd";
import moment from 'moment';

const Timer = ({defaultTime, timerStopped$, currentSection, setCurrentSection}) => {
    const [sections] = useSessionStorageState("sectionData");
    const [time, setTime] = useSessionStorageState("timeLeft", moment.duration(sections["sections"][currentSection]["time"], "minutes").asMilliseconds());
    const [verbosity, setVerbosity] = useSessionStorageState("timeVerbosity", false);
    const [timerActive, setTimerActive] = useSessionStorageState("timerActive", false);
    const timeStarted = useRef(Date.now());

    useEffect(() => {
        if (time === undefined) {
            setTime(moment.duration(sections["sections"][currentSection]["time"], "minutes").asMilliseconds());
        }
    }, [currentSection, sections, setTime, time]);

    const timerRef = useRef();

    const handleStart = () => {
        setTimerActive(true);
        timerStopped$.emit("started")
        timerRef.current.start();
        message.success("Started the timer! Good Luck!");
    };
    const handleStop = () => {
        setTimerActive(false);
        timerStopped$.emit("stopped");
        timerRef.current.pause();
        message.error("Stopped the timer! Start it to keep going!")
    };


    const renderer = ({total, hours, minutes, seconds, completed, formatted}) => {
        sessionStorage.setItem("timeLeft", total)
        if (completed) return <h3 className={"timer-text"}>DONE</h3>;
        else if (verbosity) return <span>{hours} hours {minutes} minutes {seconds} seconds left</span>;
        else return <span>{formatted.hours}:{formatted.minutes}:{formatted.seconds} left</span>;
    }

    const TimerMenu = () => {
        return (
            <div>
                <Button type={"danger"} onClick={handleStop} style={{marginRight: "5px"}}
                        disabled={!timerActive}>Stop</Button>
                <Button type={"primary"} onClick={handleStart} disabled={timerActive}>Start</Button>
            </div>
        )
    }

    return (
        <Popover
            content={TimerMenu}
            placement={"bottomRight"}
            title={"Timer Menu"}
        >
            <div className={"timer-text"}
                 onClick={() => setVerbosity(!verbosity)}
            >
                <Countdown
                    date={Number(timeStarted.current + time)}
                    autoStart={timerActive}
                    ref={timerRef}
                    renderer={renderer}
                    zeroPadTime={2}
                />
            </div>
        </Popover>

    )
}

export default Timer;