import React from "react";
import './BubbleRow.css';


const Bubble = ({letter, isSelected, number, disabled}) => {
    return (
        <label >
            <input type={"radio"}
                   name={`question-${number}`}
                   value={letter}
                   id={number}
                   className={`bubble ${isSelected ? "selected" : ""} ${disabled ? "disabled": ""}`}
                   disabled={disabled}
            />
            <span className={`${disabled ? "disabled-text" : ""}`}>{letter}</span>
        </label>
    )
}

export default Bubble;