import React, {useEffect, useState} from "react";
import {Layout} from "antd";
import ExamHeader from "./ExamHeader";
import ExamContent from "./ExamContent";
import './Exam.css';
import {useEventEmitter, useSessionStorageState} from "@umijs/hooks";
import moment from 'moment';

const Exam = () => {
    const [sections] = useSessionStorageState("sectionData");
    const [currentSection, setCurrentSection] = useSessionStorageState("currentSection", 0);
    const [numberQuestions, setNumberQuestions] = useState();
    const [sectionTime, setSectionTime] = useState(0);
    const timerStopped$ = useEventEmitter();

    useEffect(() => {
        const temp = sections["sections"][currentSection]
        setNumberQuestions(temp["num_questions"]);
        setSectionTime(moment.duration(temp["time"], "minutes").asMilliseconds());
    }, [currentSection, numberQuestions, sections])

    return (
        <>
            <Layout style={{height: "100vh"}}>
                <ExamHeader timerStopped$={timerStopped$} setCurrentSection={setCurrentSection} currentSection={currentSection} time={sectionTime}/>
                <ExamContent number={numberQuestions} timerStopped$={timerStopped$}/>
            </Layout>
        </>
    );
}

export default Exam;