import React, {useState} from "react";
import {Button, Layout, Typography} from "antd";
import BubbleRow from "../BubbleRow/BubbleRow";
import {useEventEmitter} from "@umijs/hooks";
import {Modal} from "antd";

const {Paragraph} = Typography;
const {Content} = Layout;

const ExamContent = ({number, timerStopped$}) => {
    const enableButton$ = useEventEmitter();
    const [disableButton, setDisableButton] = useState(true);
    const [modalVisible, setModalVisible] = useState(false);
    const [marked, setMarked] = useState([]);
    const myArr = [];
    for (let i = 1; i <= number; i++) {
        myArr.push(i);
    }

    enableButton$.useSubscription(val => setDisableButton(val))

    const handleNextSection = () => {
        setMarked(Object.entries(JSON.parse(sessionStorage.getItem("markedQuestions"))).filter(([number, isMarked]) => isMarked));
        setModalVisible(true);
    }

    const ModalInfo = () => {
        return (
            <>
                <Paragraph>
                    Are you sure that you're done with this section? If you still have time, I recommend you double
                    check your answers!
                </Paragraph>
                {!(marked.length === 0) ? (
                    <>
                        You left the following questions marked for review:
                        <Paragraph ellipsis={{rows: 5, expandable: true, symbol: 'See all marked questions'}}>
                            <ul>
                                {marked.map(([number, isMarked]) => <li>Question {number}</li>)}
                            </ul>
                        </Paragraph>
                        You can choose to continue without going back, but I recommend the opposite.
                    </>
                ) : null}
            </>
        )
    }

    return (
        <div>
            <Content style={{padding: '0 50px', marginTop: 64}}>
                <div className={"content"}>
                    {myArr.map(num => <BubbleRow enableButton$={enableButton$} timerStopped$={timerStopped$}
                                                 number={num} totalQuestions={number} key={num}/>)}
                </div>
                <Modal title={!(marked.length === 0) ? "You left some marked questions..." : "Obligatory Confirmation"}
                       visible={modalVisible}
                       onCancel={() => setModalVisible(false)}
                       cancelText={"On second thought..."}
                       okText={"I'm ready to move on!"}
                >
                    <ModalInfo/>
                </Modal>
                <Button type={"primary"} disabled={disableButton} onClick={handleNextSection} block>I'm done with this
                    section!</Button>
            </Content>
        </div>


    );
}

export default ExamContent;