import React from "react";
import {Typography, Divider, Tooltip, Row, Col} from "antd";

const {Paragraph, Text, Title} = Typography;

const FirstParagraph = () => {
    return (
        <>
            <Divider orientation={"left"}>Exam Simulator is built on the front end.</Divider>
            <Text>
                The choice of making exam simulator was clear for me to build it on the front end. I wouldn't
                have to pay for hosting services, so I can afford to make this tool free. But since this site
                doesn't have a back end service to connect to, I can't add features like users and accounts.
            </Text>
        </>
    )
}

const SecondParagraph = () => {
    return (
        <>
            <Divider orientation={"left"}>What is the need of a back end?</Divider>
            <Text>
                For this project, I thought this exactly as well. This tool is designed for you to quickly use
                and then do as you wish. This doesn't fit the criteria for a need of a back end server. A back end
                essentially manages all the data of the server. This includes user accounts, user data, test data,
                and other data that is relevant to the site. But since this site is designed to only provide a quick
                use for tests, using a back end isn't that important as all the data that is relevant to you can be
                saved locally on your computer.
            </Text>
        </>
    )
}

const ThirdParagraph = () => {
    return (
        <>
            <Divider orientation={"left"}>What are the pros and cons of having Exam Simulator on the front
                end?</Divider>
            <Text>
                <Row>
                    <Col span={12}>
                        <Title level={4}>Pros: </Title>
                        <ul>
                            <li>Free to use</li>
                            <li><Tooltip title={"You don't need an account to use"}>Simple to use</Tooltip></li>
                            <li><Tooltip title={"Your data doesn't leave your computer"}>100% private</Tooltip></li>
                            <li><Tooltip title={"All the computations is handled on YOUR computer, not a server"}>100%
                                secure</Tooltip></li>
                            <li><Tooltip title={"You can edit and view your own data."}>Easily "hackable"</Tooltip></li>

                        </ul>
                    </Col>
                    <Col span={12}>
                        <Title level={4}>Cons: </Title>
                        <ul>
                            <li><Tooltip title={"Since we don't use a backend, theres no way to store accounts"}>No
                                accounts</Tooltip></li>
                            <li><Tooltip title={"Wherever you take the test is where the data for it is stored"}>No
                                picking up where you left off across devices</Tooltip></li>
                        </ul>
                    </Col>
                </Row>
            </Text>
        </>
    )
}

const FourthParagraph = () => {
    return (
        <>
            <Divider orientation={"left"}>Too Long, Didn't Read</Divider>
            <Text>
                In a nutshell, the tests you take with Exam Simulator are completely private, but that also means that
                you can't access them from anywhere else other than the same exact <strong>browser</strong> that you used
                to take them.

                Now that you are familiar with the drawbacks of this tool, click "I understand the above" to continue.
            </Text>
        </>
    )
}

const DisclaimerText = () => {
    return (
        <>
            <Paragraph>
                <FirstParagraph/>
                <SecondParagraph/>
                <ThirdParagraph/>
                <FourthParagraph/>
            </Paragraph>
        </>
    )
}

export default DisclaimerText;